export default {
	data() {
		return {
			
		};
	},
	methods:{

		navTo(){
			uni.navigateTo({
				url:"/pages/set/modify_password"
			})
		},
		//退出登录
		toLogout(){
			uni.showModal({
				title: '提示',
				content: '确定退出登录吗?',
				success: function (res) {
				        if (res.confirm) {
							 console.log('用户点击确定');
							 uni.removeStorageSync('USERID');
				            //uni.reLaunch({ url: '/pages/login/login' });
							uni.switchTab({
								url:'../product/product'
							})
				        } else if (res.cancel) {
				             console.log('用户点击取消');
				        }
				    }
			})
		}
		

	}
}
